
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Search from 'vux/src/components/search/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XNumber from 'vux/src/components/x-number/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Card from 'vux/src/components/card/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'


// function offsetTop (el, offset) {
// }

export default {
  mounted () {
  },
  components: {
    Group,
    Cell,
    Search,
    XButton,
    XNumber,
    Tab,
    TabItem,
    Popup,
    Card,
    Checker,
    CheckerItem
  },
  methods: {
    onSubmit () {

    },
    onFocus () {

    },
    onCancel () {

    },
    height () {
      return `${window.innerWidth}px`
    },
    log (msg) {
      console.log(msg)
    },
    addCart () {
      this.show = true
    },
    go () {
      let self = this
      let $router = this.$router
      self.show = false
      $router.push('/cart')
    },
    scrollTo (selector) {
      let self = this
      let top = 0
      let tag
      if (selector) {
        tag = self.$el.querySelector(selector)
        top = tag.offsetTop
      }
      self.$refs.scroller.scrollTo(0, top, true)
    }
  },
  data () {
    return {
      title: 'goods detail',
      show: false,
      $scroller: null,
      swiperSlides: [{
        img: 'https://cdn.xiaotaojiang.com/uploads/82/1572ec37969ee263735262dc017975/_.jpg',
        title: '送你一朵fua'
      }, {
        img: 'https://cdn.xiaotaojiang.com/uploads/82/1572ec37969ee263735262dc017975/_.jpg',
        title: '送你一辆车'
      }, {
        img: 'https://cdn.xiaotaojiang.com/uploads/82/1572ec37969ee263735262dc017975/_.jpg',
        title: '送你一次旅行'
      }],
      swiperOption: {
        autoplay: 3500,
        setWrapperSize: true,
        pagination: '.swiper-pagination',
        paginationClickable: true,
        mousewheelControl: true,
        observeParents: true
      },
      autoFixed: false,
      list: [
        'https://o5omsejde.qnssl.com/demo/test1.jpg',
        'https://o5omsejde.qnssl.com/demo/test2.jpg',
        'https://o5omsejde.qnssl.com/demo/test9.jpg',
        'https://o5omsejde.qnssl.com/demo/test4.jpg',
        'https://o5omsejde.qnssl.com/demo/test5.jpg',
        'https://o5omsejde.qnssl.com/demo/test6.jpg',
        'https://o5omsejde.qnssl.com/demo/test7.jpg',
        'https://o5omsejde.qnssl.com/demo/test8.jpg'
      ]
    }
  }
}



