
import _ from 'lodash'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Box from 'vux/src/components/box/index.vue'
import Card from 'vux/src/components/card/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Icon from 'vux/src/components/icon/index.vue'


export default {
  components: {
    XInput,
    XButton,
    Box,
    Card,
    Group,
    Cell,
    Icon
  },
  methods: {
    picker () {
      let self = this
      let wx = this.$wechat
      wx.chooseImage({
        count: 1, // 默认9
        sizeType: ['original', 'compressed'], // 可以指定是原图还是压缩图，默认二者都有
        sourceType: ['camera'], // 可以指定来源是相册还是相机，默认二者都有
        success (res) {
          // res.localIds[0] // 返回选定照片的本地ID列表，localId可以作为img标签的src属性显示图片
          // 上传
          wx.uploadImage({
            localId: res.localIds[0], // 需要上传的图片的本地ID，由chooseImage接口获得
            isShowProgressTips: 1, // 默认为1，显示进度提示
            success (res) {
              self.serverId = res.serverId // 返回图片的服务器端ID
            }
          })
          // 显示
          wx.getLocalImgData({
            localId: res.localIds[0], // 图片的localID
            success (res) {
              let localData = res.localData // localData是图片的base64数据，可以用img标签显示
              localData = localData.replace('jgp', 'jpeg')// iOS 系统里面得到的数据，类型为 image/jgp,因此需要替换一下
              self.ticketUrl = localData
            },
            fail (res) {
              self.alert('显示失败')
            }
          })
        }
      })
    },
    submit () {
      let self = this
      let $http = this.$http
      let fields = [
        'amount',
        'serverId'
      ]
      let data = _.merge({}, _.pick(self.$data, fields))
      if (!data.serverId) {
        self.alert('请先拍摄上传小票')
        return
      }
      if (!data.amount) {
        self.alert('请输入消费金额')
        return
      }
      $http.post('a/api/bill', data).then(res => {
        let response = res.body
        self.alert(response.status.info)
      })
    },
    alert (message) {
      const alert = this.$vux.alert
      alert.show({
        title: '提示',
        content: message
      })
    }
  },
  data () {
    return {
      title: '',
      ticketUrl: '',
      amount: '',
      serverId: ''
    }
  }
}



