
import XButton from 'vux/src/components/x-button/index.vue'
import Checklist from 'vux/src/components/checklist/index.vue'
import Box from 'vux/src/components/box/index.vue'
import Card from 'vux/src/components/card/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Icon from 'vux/src/components/icon/index.vue'


export default {
  created () {
    let self = this
    self.load()
  },
  mounted () {
    let self = this
    let $route = this.$route
    self.isSelect = $route.params['select']
  },
  components: {
    XButton,
    Checklist,
    Box,
    Card,
    Group,
    Cell,
    Icon
  },
  methods: {
    load () {
      let self = this
      let $http = this.$http
      $http.post(`a/api/address/list`, {}).then(res => {
        let response = res.body
        let list = response.results.list
        for (let i = 0; i < list.length; i++) {
          list[i].isDefault = list[i].isDefault === '1'
        }
        self.list = list
      })
    },
    editor (id) {
      let $router = this.$router
      if (id) {
        $router.push(`address/${id}`)
      } else {
        $router.push('address/0')
      }
    },
    dele (id) {
      let self = this
      let $http = this.$http
      self.confirm('确定要删除？', flag => {
        if (flag) {
          $http.delete(`a/api/address/${id}`, {id: id}).then(res => {
            let response = res.body
            self.alert(response.status.info)
            self.load()
          })
        }
      })
    },
    select (item) {
      let self = this
      let $router = this.$router
      let $store = this.$store
      $store.commit('selected', {data: item})
      if (self.isSelect) {
        $router.go(-1)
      }
    },
    confirm (message, callback) {
      this.$vux.confirm.show({
        // 组件除show外的属性
        title: '确认',
        content: message,
        onCancel () {
          callback(false)
        },
        onConfirm () {
          callback(true)
        }
      })
    },
    alert (message) {
      const alert = this.$vux.alert
      alert.show({
        title: '提示',
        content: message
      })
    }
  },
  data () {
    return {
      title: '',
      list: [],
      isSelect: false
    }
  }
}



